<template>
  <div class="content">
    <div class="category">
      <div
        :class="{
          category__item: true,
          'category__item--active': currentTab === item.tab,
        }"
        v-for="item in categories"
        :key="item.name"
        @click="() => handleTabClick(item.tab)"
      >
        {{ item.name }}
      </div>
    </div>
    <div class="product">
      <div class="product__item" v-for="item in list" :key="item.id">
        <img class="product__item__img" :src="item.imgUrl" />
        <div class="product__item__detail">
          <h4 class="product__item__title">{{ item.name }}</h4>
          <p class="product__item__sales">月售 {{ item.sales }} 件</p>
          <p class="product__item__price">
            <span class="product__item__yen">&yen;</span>{{ item.price }}
            <span class="product__item__origin">&yen;{{ item.oldPrice }}</span>
          </p>
        </div>
        <div class="product__number">
          <span
            class="product__number__minus iconfont"
            @click="
              () => {
                changeCartItem(shopId, item.id, item, -1, shopName);
              }
            "
            >&#xe691;</span
          >
          {{ getProductCartCount(shopId, item.id) }}
          <span
            class="product__number__plus iconfont"
            @click="
              () => {
                changeCartItem(shopId, item.id, item, 1, shopName);
              }
            "
            >&#xe668;</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watchEffect, onMounted } from "vue";
import { useRoute } from "vue-router";
import { useCartStore } from "../../store/useCartStore";
import { get } from "../../utils/request";
import { useCommonCartEffect } from "../../effects/cartEffects";
import { ElMessage } from "element-plus";

const useCurrentListEffect = (currentTab, shopId) => {
  const list = ref([]);
  const getContentData = async () => {
    const result = await get(`/api/shop/${shopId}/products`, {
      tab: currentTab.value,
    });
    if (result?.errno === 0 && result?.data?.length) {
      list.value = result.data;
    }
  };
  watchEffect(() => {
    getContentData();
  });
  return { list };
};

// Tab 切换相关的逻辑
const useTabEffect = (categories) => {
  const currentTab = ref("");
  watchEffect(() => {
    if (categories.value.length) {
      currentTab.value = categories.value[0].tab;
    }
  });
  const handleTabClick = (tab) => {
    currentTab.value = tab;
  };
  return { currentTab, handleTabClick };
};

// 购物车相关逻辑
const useCartEffect = () => {
  const store = useCartStore();
  const { cartList, changeCartItemInfo } = useCommonCartEffect();
  const changeShopName = (shopId, shopName) => {
    store.changeShopName({ shopId, shopName });
  };
  const changeCartItem = (shopId, productId, item, num, shopName) => {
    changeCartItemInfo(shopId, productId, item, num);
    changeShopName(shopId, shopName);
  };
  const getProductCartCount = (shopId, productId) => {
    return cartList?.[shopId]?.productList?.[productId]?.count || 0;
  };
  return { cartList, changeCartItem, getProductCartCount };
};

export default {
  name: "Content",
  props: ["shopName"],
  setup() {
    const route = useRoute();
    const shopId = route.params.id;
    const categories = ref([
      {
        tab: "all",
        name: "全部",
      },
    ]);
    const { currentTab, handleTabClick } = useTabEffect(categories);
    const { list } = useCurrentListEffect(currentTab, shopId);

    onMounted(async () => {
      try {
        const result = await get(`api/shop/${shopId}`);
        if (result?.errno === 0 && result?.data) {
          categories.value = result.data.tabs;
        }
      } catch (error) {
        ElMessage.error(error.message || "获取店铺信息失败");
      }
    });
    const { changeCartItem, cartList, getProductCartCount } = useCartEffect();
    return {
      categories,
      currentTab,
      handleTabClick,
      list,
      shopId,
      changeCartItem,
      cartList,
      getProductCartCount,
    };
  },
};
</script>

