import { defineStore } from "pinia";

const setLocalCartList = cartList => {
  const cartListString = JSON.stringify(cartList);
  localStorage.cartList = cartListString;
};

const getLocaCartList = () => {
  try {
    return JSON.parse(localStorage.cartList);
  } catch (e) {
    return {};
  }
};

export const useCartStore = defineStore("cart", {
  state: () => ({
    cartList: { ...getLocaCartList() }
  }),
  actions: {
    changeCartItemInfo(payload) {
      const { shopId, productId, productInfo, num } = payload;
      let shopInfo = this.cartList[shopId] || {
        shopName: "",
        productList: {}
      };
      let product = shopInfo.productList[productId];
      if (!product) {
        productInfo.count = 0;
        product = productInfo;
      }
      product.count = product.count + num;
      if (num > 0) {
        product.check = true;
      }
      if (product.count < 0) {
        product.count = 0;
      }
      shopInfo.productList[productId] = product;
      this.cartList[shopId] = shopInfo;
      setLocalCartList(this.cartList);
    },
    changeShopName(payload) {
      const { shopId, shopName } = payload;
      const shopInfo = this.cartList[shopId] || {
        shopName: "",
        productList: {}
      };
      shopInfo.shopName = shopName;
      this.cartList[shopId] = shopInfo;
      setLocalCartList(this.cartList);
    },
    changeCartItemChecked(payload) {
      const { shopId, productId } = payload;
      const product = this.cartList[shopId].productList[productId];
      product.check = !product.check;
      setLocalCartList(this.cartList);
    },
    cleanCartProducts(shopId) {
      this.cartList[shopId].productList = {};
      setLocalCartList(this.cartList);
    },
    setCartItemsChecked(shopId) {
      const products = this.cartList[shopId].productList;
      if (products) {
        for (let key in products) {
          const product = products[key];
          product.check = true;
        }
      }
      setLocalCartList(this.cartList);
    },
    clearCartData(shopId) {
      this.cartList[shopId].productList = {};
      setLocalCartList(this.cartList);
    }
  }
});
